<template>
  <div class="mcontaner">
    <Header></Header>
    <div class="relative_div">
      <div class="search">  
     <div class="search_input">
      <el-input  placeholder="请输入内容" @keydown.enter.native="search" v-model="input" clearable>        
      </el-input>
      <el-button @click="search" type="success" icon="el-icon-search"></el-button>
     </div>
    </div>
    </div>
   
    <div class="block">
      <el-timeline>

        <el-timeline-item :timestamp="blog.created | mydata" placement="top" v-for="blog in blogs">
          <el-card>
            <h4>
              <router-link :to="{name: 'BlogDetail', params: {blogId: blog.id}}">
                {{blog.title}}
              </router-link>
            </h4>
            <p>{{blog.description}}</p>
          </el-card>
        </el-timeline-item>

      </el-timeline>

      <el-pagination class="mpage"
                     background
                     layout="prev, pager, next"
                     :current-page="currentPage"
                     :page-size="pageSize"
                     :total="total"
                     @current-change=page>
      </el-pagination>

    </div>

  </div>
</template>

<script>
  import Header from "../components/Header";

  export default {
    name: "Blogs.vue",
    components: {Header},
    data() {
      return {
        input:'',
        blogs: {},
        currentPage: 1,
        total: 0,
        pageSize: 5
      }
    },
    methods: {
      page(currentPage) {
        const _this = this
        _this.$axios.get("/blogs?currentPage=" + currentPage).then(res => {
          // console.log(res)
          _this.blogs = res.data.data.records
          _this.currentPage = res.data.data.current
          _this.total = res.data.data.total
          _this.pageSize = res.data.data.size

        })
      },
      search(){
      this.$axios({
        method:'GET',
        url:'/blogs/search',
        params:{
          search:this.input
        }
      }).then(res=>{
        // console.log(res)
        this.blogs = res.data.data.records
          this.currentPage = res.data.data.current
          this.total = res.data.data.total
          this.pageSize = res.data.data.size
      })
    }
    },
    created() {
      this.page(1)
    },
    filters:{
      mydata(value){
        // console.log(value)
        return value.replace("T"," ")
      }
    }
  }
</script>

<style scoped>
.relative_div{
  position: relative;
}
.search{
  position: absolute;
    top: -112px;
    right: 0px;
}
.search_input{
  position: relative;
}
.search_input .el-input{
  width: 200px;
  position: absolute;
  top: 0;
  right: 60px;
}
.search_input input{
  position: absolute;
  top: 0;
  right: 0;
}
  .mpage {
    margin: 0 auto;
    text-align: center;
  }

</style>